import { createStore } from 'vuex'

export default createStore({
  state: {
    user: {},
    goodId: '',
  },
  getters: {
  },
  mutations: {
    setUser(state, user) {
      state.user = user;
    },
    setUserParams(state, params) {
      for (let key in params) {
        if (state.user[key]) state.user[key] = params[key];
      }
    },
    setGoodId(state, goodId) {
      state.goodId = goodId;
    }
  },
  actions: {
  },
  modules: {
  }
})
